<script>
import { db } from "@/services";
import { collection, onSnapshot, addDoc, query, orderBy } from "firebase/firestore";
import TheModal from "./TheModal.vue";

export default {
    name: 'MainPage',
    props: {
        user: String,
    },
    components: {
        TheModal
    },
    data() {
        return {
            countDownDate: new Date("Oct 1, 2022").getTime(),
            days: '00',
            hours: '00',
            minutes: '00',
            seconds: '00',
            open: false,
            play: false,
            msg: '',
            colors: [
                "red",
                "blue",
                "green",
                "indigo",
                "purple",
                "teal",
                "orange",
                "brown",
                "deep-orange",
                "blue-grey",
                "cyan"
            ],
            chat: {
                nama: "",
                ucapan: "",
                ket: "",
            },
            chats: [],
            openinvitation: false

        }
    },
    mounted() {
        this.countDownTimer();
        onSnapshot(query(collection(db, "data"), orderBy('waktu', 'desc')), (querySnapshot) => {
            const dataChat = [];
            querySnapshot.forEach((doc) => {
                const pesan = {
                    nama: doc.data().nama,
                    ucapan: doc.data().ucapan,
                    ket: doc.data().ket,
                    waktu: doc.data().waktu,
                    warna: this.colors[Math.floor(Math.random() * this.colors.length)]
                }
                dataChat.push(pesan)
            });
            this.chats = dataChat
        });
        this.openinvitation = true;
    },
    created() {
        this.countDownTimer();

    },
    watch() {
        this.countDownTimer();
    },
    methods: {
        instagramAction(data) {
            window.location = data;
        },
        copyData(data, msg) {
            navigator.clipboard.writeText(data);
            this.open = true
            this.msg = msg;
        },
        countDownTimer() {
            window.setInterval(() => {
                var now = new Date().getTime();
                var distance = this.countDownDate - now;
                var doubleDigitParse = function (digit) {
                    if (digit < 10) {
                        return '0' + digit;
                    } else {
                        return digit;
                    }
                }
                this.days = Math.floor(distance / (1000 * 60 * 60 * 24));
                this.hours = doubleDigitParse(Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60)));
                this.minutes = doubleDigitParse(Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60)));
                this.seconds = doubleDigitParse(Math.floor((distance % (1000 * 60)) / 1000));

                if (distance < 0) {
                    this.days = '00';
                    this.hours = '00';
                    this.minutes = '00';
                    this.seconds = '00';
                }
            }, 1000)

        },

        closeModal() {
            this.open = false
        },
        async sendChat(event) {
            event.preventDefault()
            var today = new Date();
            var dd = String(today.getDate()).padStart(2, '0');
            var mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
            var yyyy = today.getFullYear();
            var hh = String(today.getHours()).padStart(2, '0');
            var ss = String(today.getMinutes()).padStart(2, '0');


            today = mm + '/' + dd + '/' + yyyy + ' ' + hh + ':' + ss;
            if (this.chat.nama == '' || this.chat.ucapan == '' || this.chat.ket == '') {
                this.open = true
                this.msg = 'Silahkan isi nama, ucapan, dan keterangan terlebih dahulu'
            } else {
                await addDoc(collection(db, 'data'), {
                    nama: this.chat.nama,
                    ket: this.chat.ket,
                    ucapan: this.chat.ucapan,
                    waktu: today
                })
                this.chat.nama = ''
                this.chat.ket = ''
                this.chat.ucapan = ''
            }
        },
        goto(refName) {
            console.log('helllooo')
            var element = this.$refs[refName];
            console.log(refName)
            var top = element.offsetTop;


            window.scrollTo(0, top,);
        },
        playMusic() {
            var x = document.getElementById("music");
            x.play();
            this.play = true;
        },
        musicControl() {
            var x = document.getElementById("music");
            if (this.play) {
                x.muted = true;
                this.play = false;
            } else {
                x.muted = false;
                this.play = true;
            }
        },
        closeModalInvition() {
            this.openinvitation = false
            this.playMusic();
        },




    }
}
</script>

